import './App.css';
import React, { lazy, createContext, useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Navbar, Nav, Container, Button, Row, Col } from 'react-bootstrap';
import bgimg from './image/bg.png'
import data from './data/data.js'
import {Routes, Route, Link, useNavigate, Outlet} from 'react-router-dom'
import Detail from './routes/detail';
import styled from 'styled-components';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import Cart from './routes/Cart';
import { useEffect } from 'react';
import { useQuery } from 'react-query'


function App() {

  useEffect( () => {
    localStorage.setItem('watched', JSON.stringify([]))
  },[]) 

  // localstorage에 오브젝트를 넣는 편법
  let obj = {name:'kim'}
  localStorage.setItem('data',JSON.stringify(obj)) //obj를 json화 시킨다.
  let getOut = localStorage.getItem('data')
  console.log(JSON.parse(getOut).name) // JSON을 object화 시킨다.

  
  let [shoes, setShoes] = useState(data)
  let navigate = useNavigate(); //페이지 이동을 도와주는 
  
  let result = useQuery('작명', () => {
    return axios.get('https://codingapple1.github.io/userdata.json').then( (a) => {
      return a.data
    })
  }) //react-query를 이용해 ajax를 요청한다.
// 성공, 실패, 로딩중을 쉽게 파악할 수 있다.
console.log(result.data)
console.log(result.error)

  return (
    <div className="App">

        <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand href="#home">ShoeShop</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={()=>{ navigate('/')}}>Home</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/detail')}}>Detail</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/about')}}>about</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/event')}}>event</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/cart')}}>Cart</Nav.Link>

          </Nav>
          
        </Container>
      </Navbar>     


      
      <Routes>
        <Route path="/" element={<div>
          <div>
            
          <Nav className="ms-auto"> {result.isLoading ? '로딩중' : result.data.name }</Nav>
          <div className='main-bg' style={ {backgroundImage : 'url(' + bgimg + ')'}}></div>

            <div className='container'>
              <div className='row'>
              {
              shoes.map(function(a,i){ //shoes의 자료 개수만큼 Card 컴포넌트를 생성한다.
                return(
                    <Card shoes={shoes[i]} i={i+1}></Card>                    
                )
                
              }
              )
            } 
            
    </div>
  </div>
            <button onClick={()=>{axios.get('https://codingapple1.github.io/shop/data2.json').then(((result)=>
            { let copy = [...shoes, ...result.data]
              console.log(copy)
              setShoes(copy)

        }))
//          Promise.all([ axios.get('/url1'), axios.get('/url2')]) //동시에 ajax 요청을 여러개 하려면 Promise.all ( [axios.get('url')])을 사용한다.
          //두개 다 실행했을 때 .then( ) 문을 사용할 수 있다.
          //또는, axios.get을 그냥 두 개 써도 됨 

          .catch( ()=>{ //try - catch 문
            console.log('실패함')
          })
          }}>더보기</button>
  </div>
</div>}/>
        <Route path="/detail/:id" element={
        <div><Detail shoes={shoes}></Detail></div>}/>
        <Route path="*" element={<div>없는 페이지에요</div>}></Route>
        <Route path='/about' element={<div><About></About></div>}>
            <Route path='location' element={<div>location</div>}>

            </Route>

        </Route>
        <Route path='/event' element={<div><Event></Event></div>}>
         <Route path='one' element={<div><h4>첫 주문시 양배추즙 서비스</h4></div>}>
          </Route>
          <Route path='two' element={<div><h4>생일기념 쿠폰받기</h4></div>}></Route> 
        </Route>
        <Route path="/cart" element={<Cart></Cart>}>

        </Route>
        
        </Routes>
   

    </div>
  ); 
}
function Event() {
  return(
    <div>
      <h2>오늘의 이벤트</h2>
      <Outlet></Outlet>
    </div>
  )
}
function About(){
  return(
    <div>
      <h4>회사정보임</h4>
    <Outlet></Outlet>
    </div>
  )
}

function Card(props){ //파라미터에 props 추가하는것을 잊지 말자.. 
  
  let i = props.i-1
  console.log(i)

 return(
  
  // <Link to={"./detail/"+props.i}>
  <div className="col-md-4">
      <img src={'https://codingapple1.github.io/shop/shoes' + props.i + '.jpg'} width="80%"/>
      
      <p><Link to={"./detail/"+i}>{ props.shoes.title }</Link><br></br>{ props.shoes.price }</p>
      </div>
      // </Link>
 )
  

  }


export default App;
