import { Table } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { changeAge } from "../store"
import { memo, useState } from "react"


function Cart() {

  let a = useSelector((state)=>{return state}) //Redux store를 가져와준다.
  //let a에는 store에 들어있는 모든 변수가 들어있는 상태이다.
  let goods = useSelector( (state)=>{return state.goods})
  let dispatch = useDispatch()
  
  let [count, setCount] = useState(0)

    return(
        <div>
          <button onClick={()=>{setCount(count+1)
          console.log(count)}}></button>
            <Table>
         <thead>
    <tr>
      <th>#</th>
      <th>상품명</th>
      <th>수량</th>
      <th>변경하기</th>
    </tr>
  </thead>
  <tbody>
      {
       goods.map(function(a,i) {
        return (
         <tr>
          <td>{goods[i].id}</td>
          <td>{goods[i].name}</td>
          <td>{goods[i].count}</td>
          <td>
            <button onClick={() => {
              let id = goods[i].id
                dispatch(changeAge(id))
        }}>+</button>
        </td>

         </tr>
        )
       }) 
      }
  </tbody>

   

  
</Table> 
        </div>
    )
}

export default Cart