import { Button } from "bootstrap"
import { useEffect } from "react"
import React, { useState } from 'react';
import { useParams } from "react-router-dom"
import styled from "styled-components"
import data from "../data/data"
import { Nav, Tab } from "react-bootstrap";
import { useDispatch } from 'react-redux';
import { addItem } from '../store'


function Detail (props) {

  


  let [fade2, setFade2] = useState('')

  let [Delete, setDelete] = useState(1)
  let YelloBtn = styled.button`
  background: yellow;
  padding:10px`

  let[tab, setTab] = useState(0)
  let dispatch = useDispatch()

  useEffect( ()=> {

    setFade2('end')

    let a = setTimeout( ()=> {
      setDelete(0)

    }, 2000)

    let historyGoods = data[id].id //현재 페이지의 상품 id
    let gotIt = localStorage.getItem('watched') //일단 들어있는 watched를 가져온다.
    
    gotIt = JSON.parse(gotIt) //가져온 결과를 배열 형식으로 parse한다.
    gotIt.push(historyGoods) //gitItㅇㅔ 현재 페이지의 상품 id를 넣는다.

    //set = 중복을 허용하지 않는 array 자료

    gotIt = new Set(gotIt) //중복을 제거하기 위해 Set에 넣는다.
    gotIt = Array.from(gotIt) //array화 시킨다.

    localStorage.setItem('watched',JSON.stringify(gotIt))


    
    return() => { //unmount시 실행된다.
      clearTimeout(a)
      setFade2('')
      //return은 useEffect 동작 전에 실행된다
      //별명은 clean up function 
      //기존 데이터를 지울 때 주로 사용한다.
    }    


  },[]) //useEffect의 실행조건을 []에 넣을 수 있다. []사이에 변수를 넣으면, 그 변수가 변할때만 실행된다.
  //이를 defendency 라고하는데, 이 defendency가 없으면 mount시에만 실행된다. 
   let { id } = useParams() //유저가 url파라미터에 입력한 내용을 가져온다.
   let num = parseInt(id)+1

return (

<div className={"container start" + fade2}>
  <div>{Delete==1 ? <YelloBtn></YelloBtn>: null}</div>

  <div className="row">
    <div className="col-md-6">
      <img src={'https://codingapple1.github.io/shop/shoes' + num + '.jpg'} width="100%" />

    </div>
        <div className="col-md-6">
      <h4 className="pt-5">{data[id].title}</h4>
      <p>{data[id].content}</p>
      <p>{data[id].price}</p>
      <button className="btn btn-danger" onClick={()=>{
        dispatch(addItem( { id: data[id].id, name: data[id].title }))
        }}>주문하기</button> 
    </div>
  </div>

  <Nav variant="tabs"  defaultActiveKey="link0"> 
  {/* defaultActiveKey=기본으로 눌려있을 버튼 */}
    <Nav.Item>
      <Nav.Link eventKey="link0" onClick={() => {setTab(0)}}>버튼0</Nav.Link>
    </Nav.Item>
    <Nav.Item>
      <Nav.Link eventKey="link1" onClick={() => {setTab(1)}}>버튼1</Nav.Link>
    </Nav.Item>
    <Nav.Item>
      <Nav.Link eventKey="link2" onClick={()=> {setTab(2)}}>버튼2</Nav.Link>
    </Nav.Item>
</Nav>
<TabContent tab={tab}></TabContent>

</div> 
)
}

function TabContent({tab}) { //컴포넌트는 return문이 없으면 작동할 수 없다.
  
  let [fade,setFade] = useState('')
  useEffect( () => {
    
    setTimeout( ()=>{setFade('end')},1000) // fade를 end로 바꾼다.
    return () => { //cleanupFunction은 Effect전에 동작한다.
      setFade(' ')
    }
  },[tab]) 

if( tab == 0 ) { //if문은 html 바깥에서 작성해야 한다.
  return( <div className={'start '+fade}></div>)
} else if( tab== 1 ) {
  return( <div>내용 1</div>)
} else if( tab==2) {
  return( <div>내용 2</div>)
}
  
}


export default Detail