
import { createSlice } from '@reduxjs/toolkit';

let goods = createSlice({
    name:'cart',
    initialState : [    
        {id : 0, name : 'White and Black', count : 2},
        {id : 2, name : 'Grey Yordan', count : 1}
    ],

    reducers : {
        
        changeAge(state, id) {
            //  state[id.payload].count++
            // 받아온 id와 똑같은 id를 가진 오브젝트의 .count를 1 증가시켜라
            let found = state.findIndex( (data)=> {return data.id == id.payload})
            //조건식의 결과가 몇번째에 있는지 인덱스를 남겨준다.
            state[found].count++
            
        },

        addItem(state, actions) {
            state.push(actions)
        }

    }
})
export default goods