
import { createSlice } from '@reduxjs/toolkit';

let user = createSlice( //state 하나를 slice라고 부른다.
{
    // name:'state이름',
    // initialState: '실제 state 값'
    name: 'user',
    initialState: { name:'kim', age:10},

    reducers : {
        changeName(state){ //파라미터 state는 기존 state(현재는 kim)을 의미한다.
            return { name:'park', age:20}
        },
        plusAge(state, action){ //a는 state에 인수를 전달할 수 있게 한다.
                state.age += action.payload //payload를 반드시 붙여주어야 인수가 정상적으로 전달된다.
        }
    }

}
)

export default user
